import React from 'react';
import styles from './ContactForm.module.css'
import emailjs from 'emailjs-com';
import styled from 'styled-components';

const Heading=styled.div`
// color:#c4c4c4;
color:#4c4c4c;
width:20%;
min-width: 200px;
margin:10px auto;
padding-bottom: 5px;
// border-bottom: 5px solid #c4c4c4;
border-bottom: 5px solid #4c4c4c;
`
const Hide = styled.div`
  &>input{
      border:none;
      background:none;
      outline:none;
      cursor:none;
  }
  &>input:focus{
      visibility:hidden;
  //  display:none;
}
`
const service_id=process.env.REACT_APP_SERVICE_ID
const template_id=process.env.REACT_APP_TEMPLATE_ID
const user_id=process.env.REACT_APP_USER_ID


//emailjs.sendForm("service_s6klhzq", "template_qa8xywj", e.target, "user_l5byiBzCN2imZjgY2eWCB")

export const ContactForm = ({contact}) => {

    // console.log(service_id,template_id,user_id)
    const handleSubmit =e=>{
        e.preventDefault()// Prevents default refresh by the browser
    emailjs.sendForm(service_id, template_id, e.target, user_id)
    .then(result => {
    alert('Message Sent, I\'ll get back to you shortly', result.text);
    },
    error => {
    alert( 'An error occured, Plese try again',error.text)
    })
    }
    return (
        <div className={styles.Maincont}>
            <Heading><h1>Connect With Me</h1></Heading>
            <Hide>
      <input ref={contact}></input>
      </Hide>
      <div className={styles.flexing}>
        <form className={styles.contactForm} onSubmit={handleSubmit}>
            <div><label htmlFor="">Name:</label></div>
            <input type="text" placeholder="Name" name="from_name"/>
            <div><label htmlFor="" >Email:</label></div>
            <input type="Email" placeholder="Email" name="from_email"/>
            <div><label htmlFor="" >Message:</label></div>
            <textarea placeholder="Message" name="message"/>

           <div> <button className={styles.form_submit}>Submit</button></div>
          
        </form>

        <div className={styles.contactInfos}>
            <div className={styles.blanks}></div>
       
           <div className={styles.phone_email}>
           <a href="https://github.com/kamalgupta97" target="_blank">
           <img width="30px"  src="https://img.icons8.com/ios-filled/50/000000/github.png"/>
           </a>
           <a href="https://www.linkedin.com/in/kamalgupta97" target="_blank">
           <img width="30px" src="https://img.icons8.com/material-rounded/24/000000/linkedin--v2.png"/>
           </a>
           <a href="https://twitter.com/kamaldro97" target="_blank">
           <img width="30px" src="https://img.icons8.com/ios-filled/50/000000/twitter.png"/>
           </a>
           </div>
           <div className={styles.phone_email}>
            <div>  <img width="30px" src="https://img.icons8.com/ios-filled/50/000000/phone.png"/></div>
           <div><h3>Kamaldro97@gmail.com</h3></div>
           </div>
           <div className={styles.phone_email}>
            <div>
            <img src="https://img.icons8.com/metro/26/000000/new-post.png"/> 
                </div>    
            <div>
            <h3>+917500013500</h3>
            </div>
           
           </div>
          
        </div>

        </div>
        </div>
    )
}
